export default {
	data() {
		return {
			addStatus:false,
			list:[
				"黑色",
				"红色",
			],
			addValue:"",
			show: false,
			mode: 'bottom',
			mask: true, // 是否显示遮罩
			closeable: false,
			closeIconPos: 'top-right',
			sizeList:[
				{
					id:1,
					size:"颜色"
				},
				{
					id:2,
					size:"尺寸"
				},
				{
					id:3,
					size:"布料"
				},
				{
					id:4,
					size:"材质"
				},
				{
					id:5,
					size:"口味"
				},
				{
					id:6,
					size:"容量"
				},
				{
					id:7,
					size:"款式"
				},
				{
					id:8,
					size:"功效"
				},
				{
					id:9,
					size:"重量"
				},
				{
					id:10,
					size:"时间"
				},
			],
			active_index:[],
			select_sum:"0"
		}
	},
	onLoad() {

	},
	methods: {
	  btnClick() {
	  	console.log(this.show);
	  	this.show = true;
			this.select_sum = "0"
	  	console.log(this.show);
	  },
		close() {	 console.log('close');	},
		open() {	 console.log('open');	},
		addSize(){
			this.addStatus = true
		},
		add(e){
			this.addStatus = false
			for(var item of this.list){
				if(item == this.addValue){
					this.$refs.uToast.show({
										title: '已有相同规格',
										type: 'error',
									})
									this.addValue=""
									return
				}
			}
			
			this.list.push(this.addValue)
			this.addValue=""
		},
		select_item(e){
			if(this.active_index.length <2){
				
				if(e.target.dataset.id == this.active_index[0]){
				}else{
					this.active_index.push(e.target.dataset.id)
				}
			}else{
				if(e.target.dataset.id == this.active_index[0]){
					let arr=[]
					 arr = this.active_index.splice(1,1)
					this.active_index =arr;
				}else if(e.target.dataset.id == this.active_index[1]){
					let arr=[]
					 arr = this.active_index.splice(0,1)
					this.active_index =arr;
				}
			}
			this.select_sum=this.active_index.length
			console.log(this.active_index)
		},
		cancelSelect(){
			this.show = false
			this.active_index = []
		},
		comfirmSelect(){
			this.show = false
			console.log(this.active_index)
			this.active_index = []
		},
	
	}
}
